/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
  margin: 0;
  padding: 0;
}





/**
 * Basic styling
 */
body {
  font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
  color: $text-color;
  background-color: $background-color;
  -webkit-text-size-adjust: 100%;
  -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
       -o-font-feature-settings: "kern" 1;
          font-feature-settings: "kern" 1;
  font-kerning: normal;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}




@include media-query($on-palm) {
  .logo-svg {
    margin-top: 25px;
    width: 100% !important;
  }
}



@include media-query($on-phone) {
  .logo-svg {
    margin-top: 25px;
    width: 100% !important;
  }
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
  margin-bottom: $spacing-unit / 2;
}



/**
 * `main` element
 */
main {
  display: block; /* Default value of `display` of `main` element is 'inline' in IE 11. */
}



/**
 * Images
 */
img {
  max-width: 100%;
  vertical-align: middle;
}



/**
 * Figures
 */
figure > img {
  display: block;
}

figcaption {
  font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
  margin-left: $spacing-unit;
}

li {
  > ul,
  > ol {
    margin-bottom: 0;
  }
}



/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
  font-weight: $base-font-weight;
}



/**
 * Links
 */
a {
  color: $brand-color;
  text-decoration: none;

  &:visited {
    color: darken($brand-color, 15%);
  }

  &:hover {
    color: $text-color;
    text-decoration: underline;
  }

  .social-media-list &:hover {
    text-decoration: none;

    .username {
      text-decoration: underline;
    }
  }
}



.action-section {
  width: 100%;
  
}


/**
 * Blockquotes
 */
blockquote {
  color: $grey-color;
  border-left: 4px solid $grey-color-light;
  padding-left: $spacing-unit / 2;
  @include relative-font-size(1.125);
  letter-spacing: -1px;
  font-style: italic;

  > :last-child {
    margin-bottom: 0;
  }
}



/**
 * Code formatting
 */
pre,
code {
  @include relative-font-size(0.9375);
  border: 1px solid $grey-color-light;
  border-radius: 3px;
  background-color: #eef;
}

code {
  padding: 1px 5px;
}

pre {
  padding: 8px 12px;
  overflow-x: auto;

  > code {
    border: 0;
    padding-right: 0;
    padding-left: 0;
  }
}


/*
Header full

*/

.header-full {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}


.my-wrapper {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  

  
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;


}



/**
 * Wrapper
 */
.wrapper {
  max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
  max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
  margin-right: auto;
  margin-left: auto;
  padding-right: $spacing-unit;
  padding-left: $spacing-unit;
  @extend %clearfix;
  margin: 10px;
  

  @include media-query($on-laptop) {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
    max-width:         calc(#{$content-width} - (#{$spacing-unit}));
    padding-right: $spacing-unit / 2;
    padding-left: $spacing-unit / 2;
  }
}


@media screen and (max-width: 480px) {
  .leading {
    background-size: cover !important;
  }
  
}



@include media-query($on-phone) {
  .leading {
    background-image: url(/images/mobile-header-bg.png);
    background-size: contain;
    width: 100%;
  }

  .leading-text {
    color: white;
  }

}


@include media-query($on-palm) {
  .leading {
    background-image: url(/images/mobile-header-bg.png) !important;
    background-size: cover !important;
    width: 100%;
  }

  .leading-text {
    color: rgb(255, 255, 255) !important;
    font-weight: 650;
  }

}


@media screen and (min-width: 768px) {
  .leading {
    background-image: url(/images/desktop-header-bg.png) !important;
    background-size: contain !important;
    width: 100%;
  }

  .leading-text {
    color: rgb(24, 14, 14) !important;
    color: rgb(255, 255, 255) !important;
    font-weight: 650;
  }
  
  
}


@media screen and (min-width: 1024px) {
  .leading {
    background-image: url(/images/mobile-header-bg.png) !important;
    background-size: contain !important;
    width: 100%;
  }

  .leading-text {
    color: rgb(255, 255, 255) !important;
    font-weight: 650;
  }
  
  
}


/**
 * Clearfix
 */
%clearfix:after {
  content: "";
  display: table;
  clear: both;
}



/**
 * Icons
 */

.svg-icon {
    width: 16px;
    height: 16px;
    display: inline-block;
    fill: #{$grey-color};
    padding-right: 5px;
    vertical-align: text-top;
}

.social-media-list {
  li + li {
    padding-top: 5px;
  }
}



/**
 * Tables
 */
table {
  margin-bottom: $spacing-unit;
  width: 100%;
  text-align: $table-text-align;
  color: lighten($text-color, 18%);
  border-collapse: collapse;
  border: 1px solid $grey-color-light;
  tr {
    &:nth-child(even) {
      background-color: lighten($grey-color-light, 6%);
    }
  }
  th, td {
    padding: ($spacing-unit / 3) ($spacing-unit / 2);
  }
  th {
    background-color: lighten($grey-color-light, 3%);
    border: 1px solid darken($grey-color-light, 4%);
    border-bottom-color: darken($grey-color-light, 12%);
  }
  td {
    border: 1px solid $grey-color-light;
  }
}


.post-title {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.contact {
  height: 100%;
  width: 100%;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  padding: 0;
}

.leading-text , .slugline  {
 width: 100vw;
 text-align: center;

}

.confirm {
  display: flex;
  flex-direction: row;
  margin-bottom: auto;
  margin-top: auto;   
  align-items: flex-start;
}

.page-content {
  display: flex !important;
  flex-direction: column;
  justify-content: center;
  align-items: center !important;
}


.contact-form {

  height: max-content;
  width: 80%;
  background-color: #F3F3F3;
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 25px;
  padding: 16px;
  padding-bottom: 25px !important;
}

#message {
  height: 100px !important;
}

#message ,#name , #phone ,#email , #web{
  margin: 8px;
  height: 30px;
  width: 100%;
  margin-right: 40px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#hiring {
  line-height: 1;
  display: inline-flex;
  height: 16px;
  width: 16px;
  text-align: center;
  vertical-align: center;
  margin-right: 4px;
  

}



.red {
  color: red;
}

.u {
  text-decoration: underline;
}

.send {
  display: inline-flex;
  align-items: center;
  line-height: 1.2;
  border: none;
  background-color: #FAE800;
  padding: 0.7em 1em;
  font-size: 1.25em;
  cursor: pointer;
  font-weight: 500;
  margin-block-start: 20px;
}

.send::after {
  background-image: url();
}


.h-card {
  display: flex;
  justify-content: center;
  align-items: center;
  
}


.action {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}



.home-action {
  margin: 10px;
  padding: 6px;
  border: 1px solid white !important;
  background-color: #080808 !important;
//   background-color: rgb(68, 68, 68);
// }
}

.not-found {
  font-size: xx-large;
}

.error-action  {
  margin: 10px;
  padding: 6px;
  border: 1px solid white !important;
  background-color: #080808 !important;
  color: white;
  border-radius: 8px;
}

.error-action button {
  border: none;
  color: white;
  background-color: black;
  font-size: 16px;
}

.float-right {
  max-height: 100%; 
  margin: 5px;
}


@media screen and (min-width: 768px) {

  .leading-text {
    font-size: 50px;
    
    
  }

  .home-action {
    width: 200px;
    height: 50px;
    background-color: #828282;
    color: white !important;
    font-size: 20px;
    border: none;
    border-radius: 8px;
  }


  
}

.para-image {
  font: 400 16px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #111;
  -webkit-text-size-adjust: 100%;
  font-feature-settings: "kern" 1;
